<template>
  <div class="popup-box">
    <div class="msg-choose">
      <div class="choose-l">
        <img :src="pic" alt />
      </div>
      <div class="choose-r">
        <p class="price">￥{{detail.price}}</p>
        <p>
          已选：
          <span>{{msg.color}} {{msg.size}}</span>
        </p>
      </div>
    </div>
    <div class="first-choose">
      <p>颜色分类</p>
      <div class="clearfix">
        <div
          class="first-box"
          v-for="(oneMsg,i) in first"
          :key="i"
          :class="oneMsg.active"
          @click="addStyle(i,first)"
        >
          <img :src="oneMsg.img" alt />
          <span>{{oneMsg.color}}</span>
        </div>
      </div>
    </div>
    <div class="sce-choose">
      <p>尺码</p>
      <div class="clearfix">
        <div
          class="sce-box"
          v-for="(twoMsg,i) in sce"
          :key="i"
          :class="twoMsg.active"
          @click="addStyle(i,sce)"
        >{{twoMsg.size}}</div>
      </div>
    </div>
    <div class="buyNum">
      <span>购买数量：（限购5件）</span>
      <van-stepper v-model="count" integer max="5" />
    </div>
    <div class="pop-bottom">
      <button class="addCars" @click="addShopcars">加入购物车</button>
      <button class="buyNow" @click="buynow">立即购买</button>
    </div>
  </div>
</template>

<script>
import store from "@/store";
import { mapState } from "vuex";

export default {
  props: ["goodInfo"],
  data() {
    return {
      count: 1,
      pic:
        "https://m.360buyimg.com/mobilecms/s750x750_jfs/t1/63913/4/8420/195401/5d661896Eb17caa60/057ed4aa11320c4c.jpg!q80.dpg.webp",
      msg: {
        color: "",
        size: ""
      },
      first: {
        one: {
          img: this.$store.state.detail.img,
          color: "浅绿色",
          active: "false"
        },
        two: {
          img:
            "https://m.360buyimg.com/mobilecms/s750x750_jfs/t1/63913/4/8420/195401/5d661896Eb17caa60/057ed4aa11320c4c.jpg!q80.dpg.webp",
          color: "黑色",
          active: "false"
        }
      },
      sce: {
        one: { size: "S", active: "false" },
        two: { size: "M", active: "false" },
        three: { size: "L", active: "false" },
        four: { size: "XL", active: "false" },
        five: { size: "XXL", active: "false" }
      }
    };
  },
  methods: {
    addStyle(key, obj) {
      for (var item in obj) {
        obj[item].active = "false";
        if (!obj[item].color) {
          this.msg.size = "";
        } else {
          this.msg.color = "";
        }
      }
      obj[key].active = "true";
      if (!obj[key].color) {
        this.msg.size = obj[key].size;
      } else {
        this.msg.color = obj[key].color;
        this.pic = obj[key].img;
      }
      this.$store.state.argu = this.msg.color + "  " + this.msg.size;
    },
    addShopcars() {
      let usernames = JSON.parse(sessionStorage.userInfo).username;
      let goodNames = this.$route.query.name;
      var flag = true;
      for (var item in this.msg) {
        if (this.msg[item] == "") {
          flag = false;
        }
      }
      if (flag) {
        this.$axios
          .post("/vue/addShopCar", {
            username: usernames,
            goodId: this.goodInfo._id,
            goodInfo: this.msg,
            count: this.count,
            goodName: goodNames,
            img: this.pic,
            goodPrice: this.goodInfo.price
          })
          .then(res => {
            console.log(this.$parent.show )
            this.$parent.show = false;

          });
      }else{
         this.$toast('请选择分类');
      }

    },
    buynow() {}
  },
  computed: {
    ...mapState(["detail", "value", "argu"])
  }
};
</script>

<style lang="scss" scoped>
.popup-box {
  position: relative;
  padding: 0.2rem;
  box-sizing: border-box;
  width: 7rem;
  min-height: 9rem;
  .true {
    border: 1px solid red;
    color: red;
  }
  .msg-choose {
    height: 2.3rem;
    width: 100%;
    // box-sizing: border-box;
    padding: 0.2rem;
    .choose-l {
      float: left;

      img {
        height: 2rem;
        width: 2rem;
        margin-right: 0.3rem;
      }
    }
    .choose-r {
      .price {
        font-weight: bold;
        color: red;
        margin-bottom: 0.6rem;
      }
    }
    .close {
      position: absolute;
      top: 0.2rem;
      right: 0rem;
    }
  }
  .first-choose {
    width: 100%;
    padding-bottom: 0.2rem;
    margin-bottom: 0.2rem;
    border-bottom: 1px solid #eee;
    p {
      margin-bottom: 0.2rem;
    }

    .first-box {
      // line-height:
      margin-right: 0.4rem;
      min-width: 1.5rem;
      float: left;
      background: #eee;
      border-radius: 0.2rem;
      padding: 0.1rem 0.3rem;
      img {
        height: 0.4rem;
        width: 0.4rem;
        // display: inline-block;
        margin-right: 0.1rem;
        float: left;
      }
    }
  }
  .sce-choose {
    padding-bottom: 0.2rem;
    border-bottom: 1px solid #eee;
    width: 100%;
    margin-bottom: 0.2rem;
    p {
      margin-bottom: 0.2rem;
    }
    .sce-box {
      text-align: center;
      min-width: 0.8rem;
      float: left;
      margin-right: 0.2rem;
      background: #eee;
      border-radius: 0.2rem;
      padding: 0.1rem;
    }
  }
  .buyNum {
    display: flex;
    padding-bottom: 0.2rem;
    justify-content: space-between;
    border-bottom: 1px solid #eee;
  }
  .pop-bottom {
    position: absolute;
    bottom: 0.2rem;
    left: 0.2rem;
    width: 100%;

    button {
      height: 0.7rem;
      border: 0;
      width: 50%;
      color: #fff;
      font-weight: bold;
    }
    .addCars {
      background: #ffaa00;
      border-top-left-radius: 0.5rem;
      border-bottom-left-radius: 0.5rem;
    }
    .buyNow {
      background: #f50800;
      border-top-right-radius: 0.5rem;
      border-bottom-right-radius: 0.5rem;
    }
  }
}
</style>