<template>
  <div class="detail-box">
    <van-nav-bar
      :title="$route.query.name"
      right-text="首页"
      left-arrow
      @click-left="onClickLeft"
      @click-right="onClickRight"
    />
    <div class="del-box">
      <img :src="detail.img" alt class="del-img" />
      <p class="del-pri">
        <span class="pri-one">￥{{detail.price}}</span>
        <span class="pri-old">￥{{detail.price*1 + 100}}</span> 已含税
        <span class="pri-two">142人已购买</span>
      </p>
      <div class="count-down">
        距离活动结束还剩：
        <van-count-down :time="time" format="DD 天 HH 时 mm 分 ss 秒" style="color:red;font-size:16px" />
      </div>
      <p class="del-name">【官方授权】{{detail.name}}</p>
    </div>

    <div class="good-params">
      <van-cell value is-link class="freight">
        <template slot="title">
          <span class="custom-title">运费：&nbsp;&nbsp;&nbsp;订单满50元包邮</span>
        </template>
      </van-cell>
      <van-cell value is-link class="freight" @click="showPopup">
        <template slot="title">
          <span class="custom-title">参数：&nbsp;&nbsp;&nbsp;{{argu}}</span>
        </template>
      </van-cell>
      <van-popup v-model="show" position="bottom" round>
        <Popup ref="pop" :goodInfo="detail" id></Popup>
      </van-popup>
    </div>
    <div class="foot">
      <van-goods-action>
        <van-goods-action-icon icon="chat-o" text="客服" />
        <van-goods-action-icon :info="carNum" icon="cart-o" text="购物车" @click="gotocart" />
        <van-goods-action-icon icon="shop-o" text="店铺" />
        <van-goods-action-button type="warning" text="加入购物车" @click="todoAddCars" />
        <van-goods-action-button type="danger" text="立即购买" @click="todoBuynow"/>
      </van-goods-action>
    </div>
  </div>
</template>

<script>
import store from "@/store";
import Popup from "@/components/Popup.vue";
import { mapState } from "vuex";
export default {
  components: {
    Popup
  },
  data() {
    return {
      time: 30 * 60 * 60 * 1000,
      show: false,

    };
  },
  computed: {
    ...mapState(["detail", "argu","carNum"])
  },
  methods: {
    gotocart(){
      this.$router.push({name:"cart"})
    },
    onClickLeft() {
      this.$router.go(-1);
    },
    onClickRight() {
      this.$router.push({ name: "home" });
    },
    showPopup() {
      this.show = true;
    },
    todoAddCars() {
        this.$refs.pop.addShopcars()
    },
    todoBuynow() {}
  },
  mounted() {
    this.$axios
      .get("/vue/getGoodDetail", {
        params: {
          goodId: this.$route.params.goodId
        }
      })
      .then(res => {
        this.$store.state.detail = res.data.result;
        console.log(this.detail);
      });
  }
};
</script>

<style lang="scss" scoped>
.detail-box {
  padding-bottom: 1.4rem;
}
.del-box {
  .del-img {
    height: 6rem;
    width: 100%;
  }
  .del-pri {
    padding: 0.2rem;
    color: #666;
    font-size: 14px;
    .pri-one {
      font-size: 20px;
      color: #f33873;
      margin-right: 0.2rem;
    }
    .pri-old {
      text-decoration: line-through;
    }
    .pri-two {
      float: right;
    }
  }
  .del-name {
    padding: 0.2rem;
    border-bottom: 1px solid #eeeeee;
  }
  .count-down {
    padding: 0.2rem;
  }
}
.good-params {
  .freight {
    border-bottom: 1px solid #eeeeee;
    .custom-title {
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      width: 5rem;
      display: inline-block;
    }
  }
}
.foot {
  box-shadow: 0px -1px 2px #eee;
  width: 7.5rem;
  background: #fff;
  position: fixed;
  bottom: 0;
  left: 0;
  padding: 0.2rem;
  box-sizing: border-box;
  .foot-l {
    width: 2.6rem;
    display: flex;
    justify-content: space-around;
    float: left;
    font-size: 12px;
  }
}
</style>